import React from 'react';
import './Glossary.css';
import GlossaryCard from './GlossaryCard';
import MainPageHeader from '../LogoLumos/MainPageHeader';
import NavBar from '../NavBar/NavBar';
import dropdown1 from '../../Assets/Images/glossary-dropdown-1.svg';
import dropdown2 from '../../Assets/Images/glossary-dropdown-2.svg';
import dropdown3 from '../../Assets/Images/glossary-dropdown-3.svg';
import dropdown4 from '../../Assets/Images/glossary-dropdown-4.svg';
import dropdown5 from '../../Assets/Images/glossary-dropdown-5.svg';


const Glossary = () => {
    return (
      <div className='glossary-container'>
        <MainPageHeader />

        <div className='glossary-page'>
            <div className='glossary-page-upper-text-container'>
                <p className='glossary-page-upper-text'>
                    Explore nosso glossrio e descubra a importncia das informaes que vem nas embalagens dos 
                    produtos:
                </p>
            </div>

            <div className='glossary-page-cards'>
                <GlossaryCard 
                    width='100%'
                    height='40rem'
                    dropdownHeight='30rem'
                    padding='0.8rem'
                    borderRadius='1.3rem'
                    border='1px solid gray'
                    upperText='Fluxo Luminoso'
                    bottomText='Lmen (Lm)'
                    dropdownImg={dropdown1}
                    dropdownText=' a quantidade de luz emitida pelo equipamento. Deve ser ajustado  sensibilidade do olho humano para permitir a realizao de tarefas sem causar ofuscamento. O clculo do fluxo luminoso depende das dimenses do espao e dos materiais de revestimento, bem como das atividades que so realizadas no local. Existem tabelas que recomendam o nvel de iluminao do ambiente (lux), a relao entre o fluxo luminoso e a rea da unidade de sala (lmen / m). Nosso aplicativo vai fazer esse clculo para lhe ajudar a obter a quantidade de lmen para seu ambiente.'
                />

                <GlossaryCard 
                    width='100%'
                    height='40rem'
                    dropdownHeight='30rem'
                    padding='0.8rem'
                    borderRadius='1.3rem'
                    border='1px solid gray'
                    upperText='Fluxo Energtico ou Potncia'
                    bottomText='Watts (w)'
                    dropdownImg={dropdown2}
                    dropdownText=' a quantidade de energia eltrica consumida que o equipamento precisa para emitir determinada quantidade de luz (fluxo luminoso). Isso mostra a demanda de instalao de sistema de iluminao eltrica e o consumo eltrico, bem como, a eficincia energtica do equipamento. Quanto menos eletricidade o equipamento precisar para a emisso daquela quantidade de fluxo luminoso, maior ser sua eficincia energtica ou seu rendimento luminoso *** (Lm/W).'
                />

                <GlossaryCard 
                    width='100%'
                    height='45rem'
                    dropdownHeight='35rem'
                    padding='0.8rem'
                    borderRadius='1.3rem'
                    border='1px solid gray'
                    upperText='Intensidade Luminosa'
                    bottomText='Candela (Cd)'
                    dropdownImg={dropdown3}
                    dropdownText=' concentrao do fluxo luminoso que se distribui formando um ngulo slido (o ngulo no espao tridimensional). Os valores de intensidade luminosa (candela) de uma fonte luminosa so expressos por uma curva, denominada curva de distribuio ou curva zonal. Esta curva depende do ngulo slido da fonte de luz e da forma do bulbo da lmpada. Cada tipo de iluminao vai exigir ngulos slidos especficos e forma de bulbo, de acordo com a necessidade de foco. No caso da luz geral, que demanda uma distribuio mais uniforme possvel do fluxo luminoso para proporcionar uma iluminao difusa, a intensidade deve ser sempre a mais baixa possvel, sendo recomendado o angulo de abertura do equipamento maior ou igual a 120. Detalhe! Quando essa informao no estiver na embalagem do produto  porque o angulo  maior ou igual a 120.'
                />

                <GlossaryCard 
                    width='100%'
                    height='50rem'
                    dropdownHeight='40rem'
                    padding='0.8rem'
                    borderRadius='1.3rem'
                    border='1px solid gray'
                    upperText='Temperatura de Cor ou Aparncia da Cor da Emisso da Luz'
                    bottomText='Kelvin (K)'
                    dropdownImg={dropdown4}
                    dropdownText='Temperatura de cor indica a aparncia de cor da fonte de luz. Uma temperatura de cor elevada tem uma aparncia branca (6500K) e uma baixa temperatura de cor tem uma aparncia amarela (3000K). Existem diferentes tipos de branco e amarelo, que so indicados pela unidade Kelvin. Uma aparncia de cor neutra est na faixa de 4000K. A aparncia branca da luz , considerada uma aparncia fria, torna as pessoas mais ativas, enquanto a aparncia amarela da luz, considerada uma aparncia quente, torna as pessoas mais relaxadas. Em geral, o grupo de lmpadas com alta temperatura de cor (luz fria)  indicado para espaos com tarefas de preciso. Por outro lado, o grupo de lmpadas com baixa temperatura de cor (luz quente)  normalmente ideal para espaos de menor concentrao, onde o interesse  fazer o lugar aconchegante. No entanto, isso varia de acordo com cada usurio. O importante  no misturar num mesmo acendimento temperaturas de cor distintas. Na duvida, escolha pela luz neutra (4000K).'
                />

                <GlossaryCard 
                    width='100%'
                    height='40rem'
                    dropdownHeight='30rem'
                    padding='0.8rem'
                    borderRadius='1.3rem'
                    border='1px solid gray'
                    upperText='ndice de Reproduo de Cor ou IRC'
                    bottomText='RA'
                    dropdownImg={dropdown5}
                    dropdownText='O indice de reproduo de cor indica quanto a luz da sua fonte (luz artificial) reproduz fielmente as cores de uma objeto ou superficie, comparado com as cores que so percebidas por voc quando esse mesmo objeto e superfcie  iluminada pela luz do sol (luz natural) ao meio dia. Em uma residncia, de modo geral, todas as fontes de luz precisam ter um IRC de bom a excelente, o que varia entre 80RA e 100RA.'
                />
            </div>

            <div className='glossary-page-bottom-text-container'>
                <p className='glossary-page-bottom-text'>
                    Essas so as propriedades lumnicas que voc precisa considerar ao adquirir uma equipamento de iluminao. Elas interagem com as superfcies iluminadas e promovem o estmulo lumnico que  captado pelo seu olho e interpretado pelo seu crebro para gerar sensaes. Se a luz percebida por voc gera sensao de bem estar, essa luz oferece conforto lumnico, isto , sade tanto fsica(sade da viso) quanto psicolgica. 
                </p>
                <p className='glossary-page-bottom-text'>
                    Sempre selecione equipamentos que so fonte de luz considerando suas unidades fotomtricas que medem essas propriedades que listamos aqui e escolha dentro dessa seleo o que apresentar maior rendimento luminoso, isto  maior eficincia energtica. No entanto nunca consuma um equipamento s pela eficincia energtica. Antes de tudo a iluminao precisa ser eficiente, isto  a luz tem que estar adequada! 
                </p>
                <p className='glossary-page-bottom-text'>
                    Lumos vai ajudar voc a encontrar a luz geral mais adequada para seu ambiente!
                </p>
            </div>
        </div>
        <NavBar colorFaq={false} colorGlossario={true} colorHome={false} colorLightMatch={false}/>
      </div>
    );
  };

export default Glossary;